import { Box, Button, Grid, Paper, Typography ,Card ,CardContent ,  Tabs,
    Tab,Chip , IconButton, Dialog, DialogTitle, DialogContent, DialogActions , InputAdornment , TextField ,  Radio , RadioGroup , FormControlLabel} from '@mui/material';
import React, { useEffect, useState } from 'react'

import Loader from '../../common/loader/Loader';
import { useLocation, useNavigate } from 'react-router-dom';
import { generateWayBill, getShipNowDetails , getCourierSingleOrder, generateWayBillPaymentGateway, verifyWayBillPaymentGateway } from '../../api/login/Login';
import StarIcon from '@mui/icons-material/Star'; 

import LockIcon from '@mui/icons-material/Lock';
import toastMessage from '../../utils/toastMessage';
import { ToastContainer } from 'react-toastify';

import ArrowBackIosNewIcon from "@mui/icons-material/ArrowBackIosNew";
import { loadScript } from '../../utils/loadScript';
import { razor_pay_key } from '../../baseUrl';
const InfoBlock = ({ title, value }) => {
  return (
    <Box flex={1}>
      <Typography
        variant="h6"
        gutterBottom
        sx={{
          // fontWeight: "600",
          fontSize: "14px",
          color: "white",
        }}
      >
        {title}
      </Typography>
      <Typography
        variant="h6"
        gutterBottom
        sx={{
          // fontWeight: "600",
          fontSize: "14px",
          color: "white",
        }}
      >
        {value}
      </Typography>
    </Box>
  );
};
const CustomTextField = (props) => (
  <TextField
    variant="outlined"
    size="small"
    fullWidth
    sx={{
      "& .MuiOutlinedInput-root": {
        height: 42,
        "& .MuiOutlinedInput-input": {
          py: 1,
        },
        "&:hover fieldset": { borderColor: "var(--bs-primary1)" },
        "&.Mui-focused fieldset": { borderColor: "var(--bs-primary1)" },
      },
    }}
    {...props}
  />
);

const CustomOrderCheckOutBox = ({title , children})=> 
  <Paper
  sx={{
    paddingX: 3,
    // borderRadius: 2,
    border: "none",
    marginTop: 0,
    boxShadow:'none',
    backgroundColor:'transparent'
    // background: "linear-gradient(135deg,  #F87A1F ,#FFCC29)",
    // boxShadow: "0 4px 12px rgba(0, 0, 0, 0.1)",
  }}
>
    <Box sx={{
      px:2,
      py:1

    }}>
     {title &&
        <Typography
    variant="h6"
    gutterBottom
    sx={{
      paddingBottom: 2,
      fontWeight: "600",
      fontSize: "18px",
      color: "var(--bs-primary1)",
    }}
  >
    {title}
  </Typography> }

  <Box   display="flex"
        sx={{
          flexDirection: {
            xs: "column",
            sm: "row",
          },
          gap: 2,
          flexWrap:'nowrap'
        }}>
          {children}
 
  </Box>
  
    </Box>



</Paper>

const CourierOrderShip = () => {
    const { search } = useLocation();
  const params = new URLSearchParams(search);
  const [loading , setLoading ] = useState(true)
  const delivery_postcode = params.get("delivery_postcode") 
  const [filter, setFilter] = useState("All");
  const order_id = params.get("order_id") ;
  const [courierList , setCourierList] = useState([])
  const [courierRes , setCouierRes] = useState()
  const [data , setData ] = useState(null)
  const navigate = useNavigate()
  const [pin , setPin] = useState()
   const [openTipModel , setOpenTipModel] = useState(false)
   const [selectedService , setSelectedService] = useState(null)
     const [paymentMethod, setPaymentMethod] = useState("wallet");
     const [tpinDialogOpen, setTpinDialogOpen] = useState(false);
     const [orderSuccessOpen, setOrderSuccessOpen] = useState(false);


  const getOrderDetails = async()=>{
       if(!delivery_postcode || !order_id)
       {
        return navigate('/order-details')
       }
       let body = {
        delivery_postcode,
        order_id
       }
       try {
        let res = await getShipNowDetails(body)
        console.log(res)
        setCourierList(res?.data?.data?.available_courier_companies || [])
        setCouierRes(res?.data?.data)
       } catch (error) {
        
       } finally{
        setLoading(false)
       }
  }

  const getSingleOrderDetails = async()=>{
    if(!delivery_postcode || !order_id)
    {
     return navigate('/order-details')
    }

    try {
     let res  = await getCourierSingleOrder({order_id :order_id })
     console.log(res , "getSingleOrderDetails")
     setData(res?.data?.data)
    //  setCourierList(res?.data?.data?.available_courier_companies || [])
    //  setCouierRes(res?.data?.data)
    } catch (error) {
     
    } 
}

    

  useEffect(()=>{
    getOrderDetails()
    getSingleOrderDetails()
  },[delivery_postcode , order_id])
  

  const filteredCouriers = courierList?.filter((courier) => {
    if (filter === "All") return true;
    if (filter === "Surface") return courier?.is_surface === true;
    if (filter === "Air") return courier?.is_surface === false;;
  });

  const tipPopClose = ()=> {
    setOpenTipModel(false)
    setPin('')
    setSelectedService(null)
  }

  const tpinOpen = (value)=> {
     setSelectedService(value)
     setOpenTipModel(true)
  }
 

  const  handleWalletPayment  =async()=> {
    if (pin?.length !== 4) {
        alert("Enter a valid 4-digit TPIN");
        return;
    }
    setLoading(true)
     let body = {
        tpin : pin,
        amount : selectedService?.freight_charge,
        courier_id:selectedService?.courier_company_id,
        shipment_id:data?.orderData?.shipment_id
     }
     try {
        let res  = await generateWayBill(body)
        toastMessage(res?.data?.error , res?.data?.message)
        if(res?.data?.statusCode === 200)
        {
          setTimeout(()=>
            navigate('/order-details')
          ,1000)
        }
       
     } catch (error) {
        
     } finally{
        setLoading(false)
     }
  }

      const loadGateWay = async(body)=>{
         
          const isLoaded = await loadScript("https://checkout.razorpay.com/v1/checkout.js");
  
          if (!isLoaded) {
              alert("Razorpay SDK failed to load. Please check your internet connection.");
              return;
          }
         
          const options = {
              key: razor_pay_key, 
              amount: body.pg_checkoutData?.amount,  
              currency: "INR",
              name: "Needu",
              description: "Payment for Courier Order",
              order_id : body?.pg_checkoutData?.id, 
              handler: async function (response) {
                
                 
                  let res  =  await verifyWayBillPaymentGateway({...response ,courier_id:  selectedService?.courier_company_id})
                  toastMessage(res?.data?.error , res?.data?.message)
                  // setLoading(false)
                  if(res?.data?.statusCode === 200)
                    {
                      setTimeout(()=>
                        navigate('/order-details')
                      ,1000)
                      
                    }
                 
              },
              prefill: {
                  name: "User Name",
                  email: "user@example.com",
                  contact: "9999999999",
              },
              theme: {
                  color: "#3399cc",
              },
          };
  
          const paymentObject = new window.Razorpay(options);
          paymentObject.on('payment.failed', async function (response){
              let res  =  await verifyWayBillPaymentGateway({...response?.error,courier_id:  selectedService?.courier_company_id })
              toastMessage(res?.data?.error , res?.data?.message)
              // setLoading(false)
              
      });
          paymentObject.open();
      }

  const handleRazorpayPayment = async()=>{
    console.log("Payment gateway hit")
    let body = {
      amount : selectedService?.freight_charge,
      courier_id:selectedService?.courier_company_id,
      shipment_id:data?.orderData?.shipment_id
   }
   setLoading(true)
          try {
               let res = await generateWayBillPaymentGateway(body)
                 if(res.data?.error === false)
                 {
                   loadGateWay(res?.data?.data)
                   setOpenTipModel(false)
                 }
             } catch (error) {
               
             }
             finally {
              setLoading(false)
             }
  }

  const handleBack = ()=>{
    navigate('/order-details')
  }







    // if(loading)
    // {
    //   return <Loader/>
    // }
    if(data?.orderData?.is_ship === true)
    {
        return (
            <>
            <CustomOrderCheckOutBox title={'Order is Already Shipped'}>

            </CustomOrderCheckOutBox>
       
            </>
        )
    }


    

  return (
    <>
    {loading && <Loader/>}
    
     <Paper
     sx={{
       padding: 3,
       borderRadius: 2,
       border: "0px solid #ccc",
       marginBottom: 4,
       // background: "linear-gradient(135deg,  #F87A1F ,#FFCC29)",
       boxShadow: "0",
     }}
   >
    <IconButton onClick={handleBack}sx={{mb:2}}>
                        <ArrowBackIosNewIcon />
                      </IconButton>
       <Box sx={{
           px:2,
           py:1
     
         }}>
         
         <Paper
  sx={{
    padding: 3,
    borderRadius: 2,
    border: "1px solid #ccc",
    marginBottom: 4,
    background: "linear-gradient(135deg,#003366c9 ,#00336678)",
    boxShadow: "0 4px 12px rgba(0, 0, 0, 0.1)",
  }}
>
  {/* Glass Effect Paper inside */}
  <Paper
    sx={{
      borderRadius: 2,
      border: "1px solid white",
      background: "#fffafa26",
      backdropFilter: "blur(10px)",
      WebkitBackdropFilter: "blur(10px)",
      boxShadow: "0 4px 20px rgba(0, 0, 0, 0.1)",
    }}
  >
    {/* Content goes here */}
    <Box sx={{ padding: 2 }}>
      <Grid container spacing={3} my={0}>
        {/* Left Section */}
        <Grid
          item
          xs={12}
          lg={6}
          my={1}
          sx={{
            borderRight: { lg: 1, xs: 0 },
            borderColor: { lg: 'white', xs: 'transparent' },
          }}
        >
          <Box
            display="flex"
            sx={{
              flexDirection: {
                xs: "column",
                sm: "row",
              },
              gap: 2,
            }}
          >
            <InfoBlock
              title="Pickup From"
              value={`${data?.wareHousedata?.city} ${data?.wareHousedata?.pincode}`}
            />
            <InfoBlock
              title="Deliver To"
              value={`${data?.orderData?.shipping_address?.shipping_city} ${data?.orderData?.shipping_address?.shipping_pincode}`}
            />
          </Box>
        </Grid>

        {/* Right Section */}
        <Grid item xs={12} lg={6} my={1}>
          <Box
            display="flex"
            sx={{
              flexDirection: {
                xs: "column",
                sm: "row",
              },
              flexWrap: "wrap",
              gap: 2,
              justifyContent: {
                sm: "space-between",
                xs: "flex-start",
              },
            }}
          >
            <InfoBlock
              title="Product Value"
              value={`₹ ${data?.orderData?.n_value}`}
            />
            <InfoBlock
              title="Payment Mode"
              value={data?.orderData?.mode || "N/A"}
            />
            <InfoBlock
              title="Dead Weight"
              value={`${data?.orderData?.weight} KG`}
            />
          </Box>
        </Grid>
      </Grid>
    </Box>
  </Paper>
  </Paper>
    
        
    
    
      {/* Filter Tabs */}
      <Tabs
        value={filter}
        onChange={(e, newValue) => setFilter(newValue)}
        textColor="primary"
        indicatorColor="primary"
        variant="scrollable"
        scrollButtons="auto"
        sx={{
            '& .MuiTab-root': {
              textTransform: 'none',
              fontWeight: 500,
              minWidth: 120,
              color: '#555',
              '&.Mui-selected': {
                color: 'var(--bs-primary1)',
                fontWeight: 'bold',
              },
            },
            '& .MuiTabs-indicator': {
              backgroundColor: 'var(--bs-primary1)',
            },
          }}
      >
        <Tab label="All" value="All" />
        <Tab label="Surface" value="Surface" />
        <Tab label="Air" value="Air" />
      </Tabs>

      {/* Courier Cards */}
      <Grid container spacing={2} mt={2}>
        {filteredCouriers.map((courier, index) => (
          <Grid item xs={12} key={index}>
            <Card variant="outlined" 
         sx={{
            borderRadius: 3,
            boxShadow: 0,
            border: courier.courier_company_id === courierRes?.recommended_courier_company_id
              ? '1px solid var(--bs-primary)'
              : 'none',
            transition: 'box-shadow 0.3s ease-in-out',
            '&:hover': {
              boxShadow: 4,
            },
          }}
  >
            {courier.courier_company_id === courierRes?.recommended_courier_company_id && (
      <Chip
      label="Recommended"
      size="small"
      sx={{
        px: 1,
        mt:2,
        // fontWeight: 'bold',
        fontSize: '0.75rem',
        borderRadius: '8px',
        backgroundColor: 'white',
        color: 'var(--bs-nav)',
        border: 'none',
    
        // textTransform: 'uppercase',
        '& .MuiChip-label': {
            color: 'var(--bs-nav)', 
            fontSize: '0.75rem',
            marginTop:'3px'
          },
      }}
      icon={<StarIcon sx={{ fontSize: 16, fill:'var(--bs-nav)' }} />}
    />
    )}
              <CardContent>
                <Grid container spacing={2} alignItems="center">
                  <Grid item xs={12} sm={4}>
                    <Typography variant="h6" fontWeight={600} fontSize={15} color='var(--bs-primary1)'>
                      {courier.courier_name}
                    </Typography>
                    <Typography color="var(--bs-nav)" sx={{opacity:0.8}} fontSize={12} fontWeight={400}>
                      {courier.mode === 0 ? 'Surface':'Air'} | Min-weight: {courier.min_weight} Kg
                    </Typography>
                    <Typography color="var(--bs-nav)" sx={{opacity:0.8}} fontSize={12} fontWeight={400}>
                      RTO Charges: ₹{courier.rto_charges}
                    </Typography>
                  </Grid>
                  <Grid item xs={6} sm={1.3} textAlign="center">
                    <Typography variant="h6" fontWeight={600} fontSize={15} color='var(--bs-primary1)'>Rating</Typography>
                    <Typography variant="h5"color="var(--bs-nav)" sx={{opacity:0.8}} fontSize={12} fontWeight={400}>
                      {courier.rating?.toFixed(2)}
                    </Typography>
                  </Grid>
                  <Grid item xs={6} sm={1.2} textAlign="center">
                    <Typography variant="h6" fontWeight={600} fontSize={15} color='var(--bs-primary1)'>Est. Pickup</Typography>
                    <Typography color="var(--bs-nav)" sx={{opacity:0.8}} fontSize={12} fontWeight={400}>{courier?.suppress_date}</Typography>
                    
                  </Grid>
                  <Grid item xs={6} sm={1.2} textAlign="center">
                    <Typography variant="h6" fontWeight={600} fontSize={15} color='var(--bs-primary1)'>Est. Delivery</Typography>
                    <Typography color="var(--bs-nav)" sx={{opacity:0.8}} fontSize={12} fontWeight={400}>{courier?.etd}</Typography>
                  </Grid>
                  <Grid item xs={6} sm={1.2} textAlign="center">
                    <Typography variant="h6" fontWeight={600} fontSize={15} color='var(--bs-primary1)'>Weight</Typography>
                    <Typography color="var(--bs-nav)" sx={{opacity:0.8}} fontSize={12} fontWeight={400}>{courier.charge_weight} Kg</Typography>
                  </Grid>
                  <Grid item xs={6} sm={1.2} textAlign="center">
                    <Typography variant="h6" fontWeight={600} fontSize={15} color='var(--bs-primary1)'>Charges</Typography>
                    <Typography color="var(--bs-nav)" sx={{opacity:0.8}} fontSize={12} fontWeight={400} variant="h6">₹{courier.finalAmount
 }</Typography>
                  
                  </Grid>
                  <Grid item xs={6} sm={1.5} textAlign="center">
                  <Button
                      variant="contained"
                      onClick={()=>tpinOpen(courier)}
                    //   sx={{ mt: 1, borderRadius: 2 }}
                      sx={{
                        borderColor: 'var(--bs-primary1)', // Custom colors
                       
                        backgroundColor: 'var(--bs-primary1)', // Custom colors
                        color: "white",

                      }}
                      fullWidth
                    >
                      Ship Now
                    </Button>
                  </Grid>
                </Grid>
              </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>
      </Box> 
     
 
 
 
 </Paper>



 <Dialog
   open={openTipModel}
   onClose={tipPopClose}
   PaperProps={{
     sx: {
       borderRadius: 4,
       p: 2,
       backdropFilter: 'blur(10px)',
       background: 'rgba(255, 255, 255, 0.9)',
       boxShadow: '0 8px 32px rgba(0, 0, 0, 0.2)',
       width: '100%',
       maxWidth: 400,
     },
   }}
 >
   {/* <DialogTitle
     sx={{
       color: 'var(--bs-primary1)',
       fontWeight: 600,
       textAlign: 'center',
       fontSize: '1.0rem',
     }}
   >
      Enter TPIN
   </DialogTitle> */}
 
   <DialogContent sx={{ textAlign: 'center' }}>
        <CustomOrderCheckOutBox title={"Payment Method"}>
              <RadioGroup
                row
                value={paymentMethod}
                onChange={(e) => setPaymentMethod(e.target.value)}
              >
                <FormControlLabel value="wallet" control={<Radio />} label="Wallet" />
                <FormControlLabel value="gateway" control={<Radio />} label="Payment Gateway" />
              </RadioGroup>
            
             
            </CustomOrderCheckOutBox>
            {paymentMethod === "wallet" && (
            <CustomTextField
                    label="TPIN"
                    type="password"
                    value={pin || ''}
                    onChange={(e) => {
                      const value = e.target.value;
                      if (/^\d{0,4}$/.test(value)) {
                        setPin(value);
                      }
                    }}
                    autoComplete="off"
                    inputProps={{ inputMode: 'numeric', maxLength: 4 }}
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position="start">
                          <LockIcon  />
                        </InputAdornment>
                      ),
                    }}
                    // sx={{ mt: 2 }}
                    fullWidth
                  /> )}

             <Button
                                        onClick={paymentMethod === "wallet" ? handleWalletPayment : handleRazorpayPayment}
                                        variant="contained"
                                        
                                        color="primary"
                                        fullWidth
                                        // disabled={loading}
                                        sx={{
                                            borderColor: 'var(--bs-primary1)', // Custom colors
                                          
                                            backgroundColor: 'var(--bs-primary1)', // Custom colors
                                            color: "white",
                                          }}
                                        style={{
                                            marginTop: "1rem",
                                            padding: "0.8rem",
                                            fontSize: "1rem",
                                            fontWeight: "bold",
                                        }}
                                    >
                                        Pay 
                                    </Button>      
   </DialogContent>
 </Dialog>
  
    </>
  )
}

export default CourierOrderShip




